<template>
  <!-- 单独抽离出的轮播图 -->
  <common-swiper class="swiperImages">
    <div slot="swiper">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(item,index) in banners" :key="index">
            <a :href="item.link" class="item-link">
              <img :src="item.image"  @load="swiperimgload"/>
            </a>
          </div>
        </div>
        <!-- 3dd Pagination -->
        <div class="swiper-pagination"></div>
      </div>
    </div>
  </common-swiper>
</template>

<script>
import commonSwiper from "components/common/swiper/commonSwiper";
export default {
  components: {
    commonSwiper
  },
  props: {
    banners: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      flag: true
    }
  },
  methods: {
    // 处理轮播图发送四次自定义事件的问题
    swiperimgload() {
      if (this.flag) {
        this.$emit("swiperimgload")
        this.flag = false
      }
    }
  }
}
</script>

<style scoped>
.swiper-slide {
  width: 100% !important;
}
.swiper-slide .item-link {
  display: block;
  width: 100%;
}
.swiper-slide img {
  width: 100% !important;
}
</style>