// 我们不希望所有的请求和参数都参杂在首页的组件里面，
// 这里是单独抽取出来管理首页的请求方便维护
import { request } from 'network/request'
export function getHome() {
  return request({
    url: '/api/hy66/home/multidata',
  })
}
// 第二个接口请求的数据
export function getHomeGoods(type, page) {
  return request({
    url: '/api/hy66/home/data',
    params: {
      type:type,
      page:page
    }
  })
}