<template>
  <div id="home">
    <!-- 组件也可以绑定class -->
    <navbar class="navbar">
      <div class="text" slot="center">
        <h2>购物街</h2>
      </div>
    </navbar>
    <!-- 这里多赋值了一份tabcontro来实现停留效果当用户滚动到一定位置时就显示出来没有滚动到则隐藏 -->
    <tab-contro
      :titles="['流行', '新款', '精选']"
      class="contro fexidshow"
      @tabclick="prentclick"
      ref="tabcontro1"
      v-show="isfexidshow"></tab-contro>
    <!-- 滚动 -->
    <scroll
      class="continers"
      ref="backTop"
      :probetype="3"
      @scroll="topshow"
      :pullUpLoad="true"
      @pullingUp="pullUp">
      <!-- swiper -->
      <swiper-item :banners="banners" @swiperimgload="swiperimgloads"></swiper-item>
      <!-- 推荐导航 -->
      <yecao-home-link :recommends="recommends"></yecao-home-link>
      <featur></featur>
      <!-- tabContro -->
      <tab-contro
        :titles="['流行', '新款', '精选']"
        class="contro"
        @tabclick="prentclick"
        ref="tabcontro2"></tab-contro>
      <!-- 导航列表 -->
      <!-- goodsall里面的代码复杂所以这里采取的是计算属性 -->
      <goods :goodsall="jumpall" />
    </scroll>
    <top @click.native="topclick" v-show="isShow"></top>
  </div>
</template>

<script>
// 公共组件
import navbar from "components/common/navbar/navbar"
import tabContro from "components/content/tabContro/tabContro"
import goods from "components/content/goods/goods"
import scroll from "components/common/scroll/scroll"
import top from "components/common/backtop/top"
// 引入home请求
import { getHome, getHomeGoods } from "network/home"
// 轮播图
import swiperItem from "./childhome/swiperItem"
// 导航
import yecaoHomeLink from "./childhome/yecaoHomeLink"
import featur from "./childhome/featur"
// 工具类函数
import { shake } from "../../tools/util"
export default {
  components: {
    navbar,
    goods,
    scroll,
    tabContro,
    top,

    swiperItem,
    yecaoHomeLink,
    featur
  },

  data() {
    return {
      // 轮播图保存的数据
      banners: [],
      recommends: [],
      // 商品列表保存的数据
      goods: {
        'pop': { page: 0, list: [] },
        'new': { page: 0, list: [] },
        'sell': { page: 0, list: [] }
      },
      jump: "pop",
      isShow: false,
      // 保存吸顶的状态 
      taboffsetTop: 0,
      isfexidshow: false,
      // 保留首页切换时的状态
      saveY: 0
      // 保存
    }
    // 默认保存pop数据通过点击然后switch动态来选择(相当于定义的是一个用来记录状态的变量)
  },
  // 组件和模板创建完实现网络请求
  created() {
    // 请求轮播图和相关要展示图片数据
    this.getHome();
    // 商品数据
    this.getHomeGoods("pop")
    this.getHomeGoods("new")
    this.getHomeGoods("sell")
  },
  methods: {
    // 网络请求
    getHome() {
      getHome().then(res => {
        // 这里面的箭头函数this指向上一级的作用域
        // 把数据请求过来保存到result里面
        // res 是大的data
        // 这里是异步请求没必要打印数据
        this.banners = res.data.banner.list
        this.recommends = res.data.recommend.list
      })
    },
    getHomeGoods(type) {
      // 引用变量type 这里的page只是为了请求数据让请求参数加1
      let page = this.goods[type].page + 1
      getHomeGoods(type, page).then(res => {
        this.goods[type].list.push(...res.data.list)
        // 第二个page+1是我们保存的数据，保存了多少个page，此时更新我们的this.goods[type].page

        this.goods[type].page += 1 
        // this.goods[type].page = page 
        // 完成上拉加载行为finishPullUp()后才能继续加载更多数据
        this.$refs.backTop && this.$refs.backTop.scroll.finishPullUp()
        // this.$refs.backTop && this.$refs.backTop.scroll.refresh()
        
      })
    },

    // 事件处理
    prentclick(index) {
      // 通过子组件传递过来的索引在通过点击让switch动态进行切换数据
      switch (index) {
        case 0:
          this.jump = "pop"
          break
        case 1:
          this.jump = "new"
          break
        case 2:
          this.jump = "sell"
          break
      }
      // 解决两个tabcontro索引不相同的问题
      this.$refs.tabcontro1.activeindex = index
      this.$refs.tabcontro2.activeindex = index
    },
    topclick() {
      // 返回顶部
      // scrollTo(x,y,缓动)
      this.$refs.backTop && this.$refs.backTop.scroll.scrollTo(0, 0, 300)
    },
    topshow(position) {
      // 当页面滑动到1000时就隐藏返回顶部按钮否则就显示
      this.isShow = Math.abs(position.y) > 1000
      // 如果动态添加类会受到batter-scroll的影响goods就会脱离标准流往上移
      // 如果大于就显示tabcontrol
      this.isfexidshow = Math.abs(position.y) >= this.taboffsetTop
    },
    pullUp() {
      this.getHomeGoods(this.jump)
      this.$refs.backTop && this.$refs.backTop.scroll.refresh()
    },

    // $el可以拿到组件的元素对象
    // 在mounted里面获取this.$refs.tabcontro.$el.offsetTop会导致获取不到具体的值因为没有把图片计算在内
    swiperimgloads() {
      this.taboffsetTop = this.$refs.tabcontro2.$el.offsetTop
    }
  },
  // 生命周期函数 这里避免多次调用refresh方法导入了防抖函数
  mounted() {
    // carted拿不到值
    // 通过事件总线
    let refresh = shake(this.$refs.backTop.refresh, 200);
    this.$bus.$on("imgitemload", () => refresh())
  },
  // 活跃时保存状态的处理函数
  activated() {
    // 解决点击切换其他选项卡保留不了首页状态的问题
    // 设置位置
    this.$refs.backTop.scroll.refresh()
    this.$refs.backTop.scroll.scrollTo(0, this.saveY, 0)
  },
  beforeRouteLeave(to, from, next) {
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
    this.saveY = this.$refs.backTop.scroll.y
    next()
  },
  // // 销毁时的函数
  // deactivated(){
  //   // 存储当前浏览的状态
  //   // 记录位置
  //   this.saveY=this.$refs.backTop.scroll.y
  // },
  computed: {
    jumpall() {
      return this.goods[this.jump].list
    }
  }
}
</script>

<style scoped>
#home {
  overflow: hidden;
  position: relative;
  height: 100vh;
}
.navbar {
  position: relative;
  top: 0;
  right: 0;
  left: 0;
  z-index: 99;
  margin: 0 auto;
  width: 100%;
  /* max-width: 750px;
  min-width: 320px; */
  background: var(--bgcolor);
}
.text h2 {
  font-size: 4.2667vw;
  font-weight: normal;
  color: #fff;
}
.contro {
  background: #fff;
}
.continers {
  position: absolute;
  top: 11.7333vw;
  bottom: 13.0667vw;
  left: 0;
  right: 0;
}
.fexidshow {
  position: relative;
  z-index: 9;
}
</style>